<template>
  <div id="myBooking" v-wechat-title="$route.meta.title">
    <div
      class="myBookingItem"
      v-for='(item, index) of items'
      :key='index'
    >
      <img v-lazy="item.picture" class="picture">
      <div class='left'>
        <span class="name"> {{ item.name }} </span>
        <span class="releaseTime"> {{ item.releaseTime }} </span>
        <span class="bookingStatus"> {{ item.bookingStatus }} </span>
      </div>
      <div class="right">
        <span class="price"> {{ item.price }} </span>
        <span class="originalPrice"><del> {{ item.originalPrice }} </del></span>
        <button
          :class='{"booking-fail":(item.bookingStatus=="")}'
          @click='cancelBooking'
        >  </button>
        <button
          @click='confirm'
        >  </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      items: [
        {
          id: '123',
          name: '',
          price: '30',
          originalPrice: '60',
          releaseTime: '12/12/11',
          bookingStatus: '',
          picture: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1517329658118&di=f4f8c1305444a40e16ef3e8c36d34b34&imgtype=0&src=http%3A%2F%2Ffile04.16sucai.com%2Fd%2Ffile%2F2015%2F0503%2F25cb8a996360eb26c0ef7bf92b234e9f.jpg'
        },
        {
          id: '123',
          name: '',
          price: '30',
          originalPrice: '60',
          releaseTime: '12/12/11',
          bookingStatus: '',
          picture: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1517329658118&di=f4f8c1305444a40e16ef3e8c36d34b34&imgtype=0&src=http%3A%2F%2Ffile04.16sucai.com%2Fd%2Ffile%2F2015%2F0503%2F25cb8a996360eb26c0ef7bf92b234e9f.jpg'
        },
      ]
    }
  },
  mounted () {
    console.log(this.$route.params.userId);
  },
  methods: {
    cancelBooking () {

    },
    confirm () {

    }
  }
}
</script>

<style lang="scss">
#myBooking {
  .myBookingItem {
    height: 100px;
    background: rgb(250, 250, 250);
    text-align: left;
    padding: 5px 10px;
    font-size: 0.9em;
    border-bottom: 2px rgb(240, 240, 240) solid;
    .picture {
      width: 80px;
      height: 80px;
      float: left;
    }
    .left {
      display: inline-block;
      width: 40%;
      padding: 0 10px;
      .name {
        color: rgb(42, 65, 75);
        display: block;
      }
      .releaseTime,
      .bookingStatus {
        font-size: 0.9em;
        color: rgb(120, 120, 120);
        display: block;
      }
    }
    .right {
      float: right;
      display: inline-block;
      width: 30%;
      text-align: center;
      .price {
        display: block;
        color: rgb(95, 95, 95);
      }
      .originalPrice {
        display: block;
        color: rgb(150, 150, 150);
      }
      button {
        font-size: 0.9em;
        border: 2px rgb(240, 240, 240) solid;
        border-radius: 10px;
        background: white;
        color: rgb(24, 152, 217);
        padding: 0 10px;
        outline: none;
        &.booking-fail {
          color: rgb(200, 200, 200);
        }
      }
    }
  }
}
</style>



