<template>
  <div id="messageChat">
    <div class="header">  </div>
    <span style="color:gray;padding:10px;">  </span>
    <MessageChatItem
      v-for='(item,index) of items'
      :key='index'
      :item='item'
    ></MessageChatItem>
  </div>
</template>

<script>
import MessageChatItem from './MessageChatItem.vue'

export default {
  components: {
    MessageChatItem
  },
  data () {
    return {
      items: []
    }
  }
}
</script>

<style lang="scss">
#messageChat {
  margin-top: 10px;
  background: white;
  text-align: left;
  .header {
    font-size: .9em;
    padding: 6px 14px;
    border-bottom: 1.5px rgb(240, 240, 240) solid;
  }
}
</style>



