<template>
  <div id="releaseMode">
    <label>  </label>
    <div class="container">
      <div class="row">
        <div class="col">
          <span
            class="mail"
            @click='setMode("mail")'
            :class="{'active':(mail==1)}"
          >  </span>
          <div class="mailPrice" v-if="mail==1">
            :
            <input
              type="number"
              oninput="if(value.length>2)value=value.slice(0,2)"
              onKeypress="return (/[\d]/.test(String.fromCharCode(event.keyCode)))"
              v-model='mailPrice'
              @input="setMode"
            >
            
          </div>
        </div>
        <div class="col">
          <span
            class="faceToFace"
            @click='setMode("faceToFace")'
            :class="{'active':(faceToFace==1)}"
          >  </span>
          <div class="faceArea" v-if="faceToFace==1">
            :
            <input
              type="text"
              maxlength="10"
              v-model='faceArea'
              placeholder="(10)"
              @input="setMode"
            >
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      mail: 0,
      faceToFace: 0,
      mailPrice: '',
      faceArea: ''
    }
  },
  methods: {
    setMode (modeValue) {
      if (modeValue == 'mail') {
        if (this.mail == 0) this.mail = 1
        else this.mail = 0
      }
      if (modeValue == 'faceToFace') {
        if (this.faceToFace == 0) this.faceToFace = 1
        else this.faceToFace = 0
      }
      var mode = {
        mail: this.mail,
        mailPrice: this.mailPrice,
        faceToFace: this.faceToFace,
        faceArea: this.faceArea
      }
      console.log(mode)
      this.$emit('transferMode', mode);
    }
  }
}
</script>

<style lang="scss">
#releaseMode {
  padding: 0 15px 15px;
  label {
    display: block;
    text-align: left;
  }
  .col {
    padding: 0;
  }
  .mail,
  .faceToFace {
    padding: 2px 15px;
    border: rgb(24, 152, 217) 1px solid;
    background: white;
    color: rgb(24, 152, 217);
    border-radius: 18px;
    &.active {
      background: rgb(24, 152, 217);
      color: white;
    }
  }
  .mailPrice {
    margin-top: 5px;
    input {
      text-align: center;
      width: 80%;
      border: none;
      border-bottom: 1px rgb(220, 220, 220) solid;
    }
  }
  .faceArea {
    margin-top: 5px;
    input {
      text-align: center;
      width: 80%;
      border: none;
      border-bottom: 1px rgb(220, 220, 220) solid;
    }
  }
}
</style>



