<template>
  <div id="messageNoticeItem">
    <p class="title"> {{ item.title }} </p>
    <p class="content"> {{ item.content }} </p>
    <p class="time"> {{ item.time }} </p>    
  </div>
</template>

<script>
export default {
  props: {
    item: {
      type: Object
    }
  }
}
</script>

<style lang="scss">
#messageNoticeItem {
  color: rgb(60, 60, 60);
  text-align: left;
  border-bottom: 1px rgb(236, 236, 236) solid;
  margin-left: 15px;
  padding: 15px 15px 0 0;
  .title {
    margin-bottom: 6px;
  }
  .content {
    font-size: 0.9em;
    margin-bottom: 6px;
  }
  .time {
    font-size: 0.7em;
    color: rgb(170, 170, 170);
    text-align: right;
    margin-right: 10px;
    margin-bottom: 6px;
  }
}
</style>


