<template>
  <div id="homeNav">
    <div class="container" :class="{'top':(isTop)}">
      <div class="row">
        <!-- all -->
        <div class="col-2 all" @click='setKind(null)'>
          <i class="iconfont icon-all"></i>
          <span>  </span>
        </div>
        <!-- kinds -->
        <div class="col-8">
          <div class="kinds">
            <div
              class='kind'
              v-for='(kind,index) of kinds'
              :key="index"
              @click='setKind(kind.id)'
              :class="{'active':(kind.id==kindId)}"
            >
              <i
                class="iconfont"
                :class="kind.icon"
              ></i>
              <span class="kindName">{{ kind.name }}</span>
            </div>
          </div>
        </div>
        <!-- search -->
        <div class="col-2 search" @click='intoSearch'>
          <i class="iconfont icon-search"></i>
          <span>  </span>
        </div>
      </div>
    </div>
  </div>
</template>

<script src="https://cdn.bootcss.com/jquery/3.2.1/jquery.js"></script>
<script>
import { getKinds } from '@/api/article.js'

var throttle = require('lodash/throttle')

export default {
  props: {
    kindId: {
      type: Number
    }
  },
  data () {
    return {
      isTop: false,
      kinds: []
    }
  },
  methods: {
    setKind (kindId) {
      this.$emit('transferKindId', kindId)
    },
    intoSearch (e) {
      this.$router.push('./search')
    },
    handleScroll () {
      let h = 45 //header
      let wh = $(window).scrollTop() //

      if (wh > h) this.isTop = true
      else this.isTop = false
    }
  },
  mounted () {
    getKinds().then(response => {
      const data = response.data
      if (data.code == 200) {
        this.kinds = data.content
      } else {
        console.error(data.message)
      }
    })
    this.$nextTick(() => {
      window.addEventListener('scroll', this.throttleScroll, false)
    })
    this.throttleScroll = throttle(this.handleScroll, 100)
  },
  deactivated () {
    window.removeEventListener('scroll', this.throttleScroll)
  }
}
</script>

<style lang="scss">
#homeNav {
  font-size: 0.9em;
  color: rgb(150, 150, 150);
  height: 50px;
  line-height: 50px;
  z-index: 99;
  .container {
    height: 50px;
    background: white;
    overflow-x: auto;
    overflow-y: hidden;
    z-index: 99;
    &.top {
      position: fixed;
      top: 0;
    }
    .row {
      .col-8 {
        padding: 0;
        overflow: auto;
      }
      .col-2 {
        padding: 0;
      }
    }
  }
  .kinds {
    width: 600px;
    .kind {
      display: inline-block;
      width: 60px;
      height: 50px;
      text-align: center;
      i {
        margin-top: -8px;
        font-size: 20px;
        display: block;
        height: 20px;
      }
      .kindName {
        font-size: 0.8em;
      }
      &.active {
        color: white;
        background: rgb(24, 152, 217);
      }
    }
  }
  .all,
  .search {
    color: rgb(24, 152, 217);
    height: 50px;
    i {
      margin-top: -8px;
      display: block;
      font-size: 20px;
      height: 20px;
      font-weight: 700;
    }
  }
  .search {
    border-left: 2px rgb(240, 240, 240) solid;
  }
}
</style>



