<template>
  <div class="feedback" v-wechat-title="$route.meta.title">
    <div class="header">
      <i class="iconfont icon-feedback_weclome"></i>
      <span class="text">  </span>
    </div>
    <div class="body">
      <textarea class="input" v-model="inputValue"></textarea>
      <button class="back" @click="back">  </button>
      <button class="confirm" @click="confirm">  </button>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      inputValue: null
    }
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    confirm () {

    }
  }
}
</script>

<style lang="scss">
.feedback {
  .header {
    color: rgb(24, 152, 217);
    margin: 5px;
  }
  .body {
    .input {
      width: 80%;
      height: 80px;
      border: 1px rgb(220, 220, 220) solid;
      border-radius: 4px;
      margin-bottom: 10px;
    }
    button {
      margin: 0 20px;
      width: 30%;
      border-radius: 4px;
      padding: 2px;
    }
    .back {
      border: 1px rgb(220, 220, 220) solid;
      background: white;
    }
    .confirm {
      background: rgb(24, 152, 217);
      color: white;
    }
  }
}
</style>


