<template>
  <div class="itemLeaveComment" @click='intoLeaveComment'>
    
  </div>
</template>

<script>
export default {
  methods: {
    intoLeaveComment () {
      this.$router.push('/leave_comment/' + this.$route.params.itemId)
    }
  }
}
</script>

<style lang="scss">
.itemLeaveComment {
  width: 100%;
  background: rgb(107, 220, 166);
  color: white;
}
</style>



