<template>
  <div class="leaveBookingMessage" v-wechat-title="$route.meta.title">
    <textarea class="leaveMessageArea" v-model="commentValue"></textarea>
    <button class="back" @click="back">  </button>
    <button class="confirm" @click="confirm">  </button>
  </div>
</template>

<script>
import { bookingItem } from '@/api/article.js'

export default {
  data () {
    return {
      commentValue: null
    }
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    confirm () {
      const id = this.$route.params.itemId
      var params = {
        message: this.commentValue
      }
      bookingItem(id, params).then(response => {
        const data = response.data
        if (data.code == 200) {
          this.$router.go(-1)
          alert('')
        } else {
          console.error(data.message)
          alert('')
        }
      })
    }
  }
}
</script>

<style lang="scss">
.leaveBookingMessage {
  .leaveMessageArea {
    height: 80px;
    width: 80%;
    border: 1px rgb(220, 220, 220) solid;
    margin: 20px;
    border-radius: 4px;
    color: rgb(70, 70, 70);
  }
  button {
    margin: 0 20px;
    width: 30%;
    border-radius: 4px;
    padding: 2px;
  }
  .back {
    border: 1px rgb(220, 220, 220) solid;
    background: white;
  }
  .confirm {
    background: rgb(248, 167, 87);
    color: white;
  }
}
</style>



