<template>
  <div class="itemCollect">
    <span @click='collect' v-if="!isColl">
      <i class="iconfont icon-collect1"></i>
      
    </span>
    <span @click='cancelCollect' v-if="isColl">
      <i class="iconfont icon-collect2"></i>
      
    </span>
  </div>
</template>

<script>
import { collectItem, cancelCollectItem } from '@/api/article'

export default {
  props: {
    itemIsColl: {
      type: Number,
      reqiured: true
    }
  },
  data () {
    return {
      isColl: this.itemIsColl
    }
  },
  methods: {
    collect () {
      collectItem(this.$route.params.itemId).then(response => {
        if (response.data.code == 200) {
          this.isColl = 1
        }
      })
    },
    cancelCollect () {
      cancelCollectItem(this.$route.params.itemId).then(response => {
        if (response.data.code == 200) {
          this.isColl = 0
        }
      })
    }
  }
}
</script>

<style lang="scss">
.itemCollect {
  font-size: 0.8em;
  display: inline-block;
  float: right;
  position: relative;
  top: -10px;
  left: -10px;
  color: orangered;
}
</style>



