<template>
  <div id='searchHistory'>
  	<span class="title">
  		
  	</span>
		<i
			class="iconfont icon-clean cleanHistory"
			@click='cleanHistory'
		></i>
  	<div class="historyList">
  		<span
  			class="historyItem"
  			v-for='(historyItem,index) of historyItems'
				:key="index"
        @click='clickItem(historyItem)'
  		>
  			{{ historyItem }}
  		</span>
  	</div>
  </div>
</template>

<script>
export default {
	data () {
		return {
			historyItems: localStorage.historyItem.split(',')
		}
	},
	methods: {
		cleanHistory (e) {
			var con;
			con = confirm("");
			if (con == true) {
				window.localStorage.removeItem("historyItem");
				this.historyItems.splice(0, this.historyItems.length);
			}
		},
		clickItem (text) {
			//
			var value = text;
			var Array = [];
			Array = localStorage.historyItem.split(',');
			//
			for (var index = 0; index < Array.length; index++) {
				if (value == Array[index]) Array.splice(index, 1);
			}
			Array.unshift(value);
			localStorage.historyItem = Array;
			//text
			this.$emit('transferInputValue', text);
			//
			this.$emit('search');
		}
	}
}
</script>

<style lang="scss">
#searchHistory {
  margin-top: 58px;
  text-align: left;
  .title {
    margin: 15px;
  }
  .cleanHistory {
    font-size: 18px;
    float: right;
    padding: 10px;
    margin-top: -10px;
    margin-right: 20px;
  }
  .historyList {
    margin-top: 10px;
    width: 80%;
    .historyItem {
      display: inline-block;
      padding: 4px 12px;
      border-radius: 18px;
      background: rgb(245, 245, 245);
      font-size: 0.9em;
      margin-bottom: 8px;
      margin-left: 18px;
    }
  }
}
</style>



