import fetch from '@/utils/fetch'

// 
export function login () {
  return fetch.get('/')
}

// 
export function getCustomer (uid) {
  return fetch.get('/customer/' + uid)
}

// 
export function getItem (id) {
  return fetch.get('/products/' + id)
}

// 
export function homeList (curr_page, catId, order_way) {
  return fetch.get('/products/all?curr_page=' + curr_page + '&catId=' + catId + '&order_way=' + order_way + '&keywords=')
}

// 
export function searchList (curr_page, keywords) {
  return fetch.get('/products/all?curr_page=' + curr_page + '&catId=&order_way=&keywords=' + keywords)
}

// 
export function postRelease (params) {
  return fetch.post('/products', params)
}

// 
export function editItem (id, params) {
  return fetch.put('/products/' + id, params)
}

// 
export function cancelItem (id) {
  return fetch.delete('/products/' + id)
}

// 
export function postLike (id) {
  return fetch.post('/products/' + id + '/likes')
}

// 
export function cancelLike (id) {
  return fetch.delete('/products/' + id + '/likes')
}

// 
export function collectItem (id) {
  return fetch.post('/products/' + id + '/collection')
}

// 
export function cancelCollectItem (id) {
  return fetch.delete('/products/' + id + '/collection')
}

// 
export function myRelease () {
  return fetch.get('/products/mine')
}

// 
export function myCollection () {
  return fetch.get('/products/collection')
}

// 
export function getKinds () {
  return fetch.get('/category/all')
}

// 
export function bookingItem (id, params) {
  return fetch.post('/order/product/' + id, params)
}

// 
export function accpetBooking (id) {
  return fetch.post('/order/' + id)
}

// 
export function refuseBooking (id) {
  return fetch.delete('/order/' + id + '/seller')
}

// 
export function cancelBooking (id) {
  return fetch.delete('/order/' + id + '/buyer')
}

// 
export function completeBooking (id, params) {
  return fetch.put('/order/' + id, params)
}

// 
export function finishBooking (id) {
  return fetch.put('/order/' + id + '/end')
}

// 
export function commentBuyer (id, params) {
  return fetch.put('/order/' + id + '/buyer/comment', params)
}

// 
export function commentSeller (id, params) {
  return fetch.put('/order/' + id + '/seller/comment', params)
}

// 
export function orderDetail (id) {
  return fetch.get('/order/' + id)
}

// 
export function myBooking () {
  return fetch.get('/order/buyer/ordering')
}

// 
export function myBought () {
  return fetch.get('/order/buyer/buy')
}

// 
export function mySold () {
  return fetch.get('/order/seller')
}

// 
export function bookingMessage (id) {
  return fetch.get('/products/' + id + '/premessage')
}

// 
export function itemComments (id) {
  return fetch.get('/comments/' + id)
}

// 
export function postComment (params) {
  return fetch.post('/comments', params)
}

// 
export function removeComment (id) {
  return fetch.delete('/comments/' + id)
}








