<template>
  <div
    id="searchResult"
  >
    <SearchResultInput
      :searchValue="searchValue"
    ></SearchResultInput>
    <div class='underInput'></div>
    <LoadingPage
      v-show='isLoading'
    ></LoadingPage>
    <ItemList
      :ItemListState='itemListState'
      :SearchValue='searchValue'
      v-show='!isLoading'
    ></ItemList>
  </div>
</template>

<script>
import SearchResultInput from './SearchResultInput.vue'
import LoadingPage from '@/components/loading_page/LoadingPage.vue'
import ItemList from '@/components/item_list/ItemList.vue'

export default {
  components: {
    SearchResultInput,
    LoadingPage,
    ItemList
  },
  data () {
    return {
      itemListState: 'search',
      searchValue: this.$route.params.item,
      isLoading: 1
    }
  },
  mounted() {
    setTimeout(()=>this.isLoading = 0,1000)
  }
}
</script>

<style lang="scss">
#searchResult {
  background: white;
  .underInput {
    width: 100%;
    height: 48px;
  }
}
</style>



