import Vue from 'vue'
import Vuex from 'vuex'
import VuexPersistence from 'vuex-persist'
import { login } from '@/api/article.js'

Vue.use(Vuex)

const state = {
  user: {},
  token: ''
}

const getters = {
  user: state => state.user,
  token: state => state.token
}

const mutations = {
  SET_USER (state, object) {
    state.user = object
  },
  SET_TOKEN (state, data) {
    state.token = data
  }
}

const actions = {
  Login ({ commit }, data) {
    return new Promise((resolve, reject) => {
      login().then.then(response => {
        const data = response.data
        if (data.code == 200) {
          const srect = result.srect
          const openId = result.openId
          const uid = result.uid
          commit('SET_TOKEN', srect)

          getCustomer(uid).then(response => {
            const data = response.data
            if (data.code == 200) {
              const name = data.content.nName // 
              const avatar = data.content.avatar // 
              const object = {
                name: name,
                avatar: avatar
              }
              commit('SET_USER', object)
            } else {
              console.error(data.message)
            }
          }).catch(error => {
            console.error(error)
          })
        } else {
          console.error(data.message)
        }
      }).catch(error => {
        console.error(error)
      })
    })
  }
}

const vuexLocal = new VuexPersistence({
  storage: window.sessionStorage,
  reducer: state => ({
    user: state.user,
    token: state.token
  }),
  filter: (mutation) => (
    mutation.type === 'SET_USER' ||
    mutation.type === 'SET_TOKEN'
  )
})

export default new Vuex.Store({
  state,
  getters,
  mutations,
  actions,
  plugins: [vuexLocal.plugin]
})



