<template>
  <div id="customer">
    
    <Customer></Customer>
    <Customer></Customer>
    <Customer></Customer>
  </div>
</template>

<script>
import { getCustomer } from '@/api/article'

export default {
  components: {

  },
  data () {
    return {
      items: {}
    }
  },
  mounted () {
    console.log(this.$route.params.id)
    const id = this.$route.params.id
    return new Promise((resolve, reject) => {
      getCustomer(id).then(response => {
        const data = response.data
        if (data.code == 200) {
          resolve(data.data)
        } else {
          reject(data.message)
        }
      }).catch(error => {
        reject(error)
      })
    }).then(result => {
      this.items = result
    })
  },
  data () {
    return {

    }
  }
}
</script>

<style lang="scss">
#customer {
}
</style>



