<template>
  <div id="messageBookingItemMessages">
    <div
      class="messages"
      v-for='(item,index) of items'
      :key='index'
    >
      <span class="userName"> {{ item.buyerName }} </span>
      <span class="message"> {{ item.message }} </span><br>
      <!-- <button class="chat">  </button> -->
      <button class="accept" @click="accept(item.orederId)">  </button>
    </div>
  </div>
</template>

<script>
import { bookingMessage, accpetBooking } from '@/api/article.js'

export default {
  data () {
    return {
      items: []
    }
  },
  mounted () {
    bookingMessage(this.$route.params.id).then(response => {
      const data = response.data
      if (data.code == 200) {
        this.items = data.content
      } else {
        this.$router.go(-1)
        console.error(data.message)
      }
    })
  },
  methods: {
    accept (id) {
      accpetBooking(id).then(response => {
        const data = response.data
        if (data.code) {
          this.$router.go(-1)
          alert('')
        } else {
          alert('')
          console.error(data.message)
        }
      })
    }
  }
}
</script>

<style lang="scss">
#messageBookingItemMessages {
  text-align: left;
  border-bottom: 1px rgb(220, 220, 220) solid;
  .messages {
    padding: 5px 10px;
    .userName {
      display: block;
      position: relative;
      left: 10px;
      color: rgb(80, 80, 80);
    }
    .message {
      display: inline-block;
      width: 80%;
      position: relative;
      left: 10px;
      color: rgb(120, 120, 120);
      font-size: 0.9em;
    }
    button {
      outline: none;
      font-size: 0.8em;
      margin-top: 5px;
      border: 1px rgb(24, 152, 217) solid;
      background: white;
      border-radius: 18px;
      color: rgb(24, 152, 217);
      padding: 0 10px;
      &.chat {
        margin-left: 50%;
      }
      &.accept {
        margin-left: 10px;
      }
    }
  }
}
</style>



