<template>
  <div id='chatInput'>
    <textarea
      name="text"
      class="inputArea"
      v-model='inputValue'
    ></textarea>
    <button @click='setInputValue(inputValue)' class="sendButton">  </button>
  </div>
</template>

<script>
export default {
  data(){
    return{
      inputValue: ''
    }
  },
  methods: {
    setInputValue(inputValue){
      this.$emit('transferInputValue',inputValue)
      this.inputValue = ''
    }
  }
}
</script>

<style lang="scss">
#chatInput {
  position: fixed;
  bottom: 0;
  height: 50px;
  width: 100%;
  background: rgb(248, 248, 248);
  box-shadow: 0px -2px 15px rgb(220, 220, 220);
  .inputArea {
    outline: none;
    resize: none;
    border: 1px solid rgb(220, 220, 220);
    border-radius: 20px;
    width: 70%;
    height: 40px;
    margin-top: 5px;
    font-size: 16px;
    font-weight: 400;
    padding: 5px 10px;
  }
  .sendButton {
    outline: none;
    float: right;
    margin-top: 10px;
    margin-right: 5%;
    padding: 2px 10px;
    border-radius: 18px;
    color: white;
    background: rgb(24,152,217);
  }
}
</style>



