<template>
  <div id="myCenter">
    <div class="header">
      
    </div>
    <div class="container">
      <div class="row row-1">
        <div
          class="col-3"
          v-for='(item,index) of [items[0],items[1],items[2],items[3]]'
   		    :key='index'
          @click='intoItem(item.name)'
          :class='{"bor-right":(index!=3)}'
        >
          <i class="iconfont" :class="item.icon"></i>
          <span class="text">{{ item.text }}</span>
        </div>
      </div>
      <div class="row">
        <div
          class="col-3"
          v-for='(item,index) of [items[4],items[5],items[6],items[7]]'
   		    :key='index'
          @click='intoItem(item.name)'
          :class='{"bor-right":(index!=3)}'
        >
          <i class="iconfont" :class="item.icon"></i>
          <span class="text">{{ item.text }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      items: [
        {
          text: '',
          icon: 'icon-myRelease',
          name: 'MyRelease'
        }, {
          text: '',
          icon: 'icon-myBooking',
          name: 'MyBooking'
        }, {
          text: '',
          icon: 'icon-mySold',
          name: 'MySold'
        }, {
          text: '',
          icon: 'icon-myBought',
          name: 'MyBought'
        }, {
          text: '',
          icon: 'icon-myCollection',
          name: 'MyCollection'
        }, {}, {}, {}
      ]
    }
  },
  methods: {
    intoItem (itemName) {
      this.$router.push({ name: itemName })
    }
  }
}
</script>

<style lang="scss">
#myCenter {
  background: white;
  margin-top: -20px;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
  .header {
    text-align: left;
    padding: 10px 15px;
    border-bottom: 2px rgb(245, 245, 245) solid;
  }
  .row-1 {
    border-bottom: 2px rgb(245, 245, 245) solid;
  }
  .col-3 {
    width: 100%;
    text-align: center;
    height: 100px;
    line-height: 50px;
    &.bor-right {
      border-right: 2px rgb(245, 245, 245) solid;
    }
    i {
      display: block;
      color: rgb(24, 152, 217);
      font-size: 2em;
      margin-top: 14px;
      margin-bottom: -15px;
    }
    .text {
      font-size: 0.7em;
      color: rgb(100, 100, 100);
    }
  }
}
</style>



