<template>
  <div id="itemList">
    <ItemListDetail
      v-for='(item,index) of items'
      :key='index'
      :item='item'
    ></ItemListDetail>
  </div>
</template>

<script>
import ItemListDetail from './ItemListDetail.vue'
import { homeList, searchList } from '@/api/article'

export default {
  components: {
    ItemListDetail
  },
  data () {
    return {
      curr_page: 1,
      catId: null,
      items: []
    }
  },
  props: {
    ItemListState: {
      type: String, // kind or search
      required: true
    },
    KindValue: {
      type: String
    },
    NewOrHotValue: {
      type: String
    },
    SearchValue: {
      type: String
    }
  },
  mounted () {
    this.$nextTick(function () {
      // 
      if (this.ItemListState == 'kind') {
        var curr_page = this.curr_page
        var catId = this.catId  // 
        var order_way = (this.NewOrHotValue == 'new') ? 'tmCreated' : 'views'
        homeList(curr_page, catId, order_way).then(response => {
          const data = response.data
          if (data.code == 200) {
            this.items.push(data.content.content)
          } else {
            console.error(data.message)
          }
        }).catch(error => {
          console.error(error)
        })
      }
      // 
      if (this.ItemListState == 'search') {
        var curr_page = this.curr_page
        var keywords = this.SearchValue
        searchList(curr_page, keywords).then(response => {
          const data = response.data
          if (data.code == 200) {
            this.items.push(data.content.content)
          } else {
            console.error(data.message)
          }
        }).catch(error => {
          console.error(error)
        })
      }
    })
  }
}

</script>

<style lang="scss">
#itemList {
  a {
    text-decoration: none;
  }
}
</style>



