<template>
  <div id="messageBookingItem">
    <img v-lazy="item.images[0]" class="picture">
    <div class='left'>
      <span class="name"> {{ item.name }} </span>
      <span class="releaseTime"> {{ item.tmCreated }} </span>
    </div>
    <div class="right">
      <span class="price"> {{ item.price }} </span>
      <span class="originalPrice"><del> {{ item.price }} </del></span>
      <button class="view" @click='intoItemMessages'>  </button>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    item: {
      type: Object
    }
  },
  methods: {
    intoItemMessages (e) {
      this.$router.push({ name: 'MessageBookingItemMessages', params: { id: this.item.id } })
    }
  }
}
</script>

<style lang="scss">
#messageBookingItem {
  height: 90px;
  background: rgb(250, 250, 250);
  text-align: left;
  padding: 5px 10px;
  font-size: 0.9em;
  border-bottom: 2px rgb(240, 240, 240) solid;
  .picture {
    width: 80px;
    height: 80px;
    float: left;
  }
  .left {
    display: inline-block;
    width: 40%;
    padding: 0 10px;
    .name {
      color: rgb(42, 65, 75);
      display: block;
    }
    .releaseTime {
      font-size: 0.9em;
      color: rgb(120, 120, 120);
    }
  }
  .right {
    float: right;
    display: inline-block;
    width: 30%;
    text-align: center;
    .price {
      display: block;
      color: rgb(95, 95, 95);
    }
    .originalPrice {
      display: block;
      color: rgb(150, 150, 150);
    }
    .view {
      font-size: 0.9em;
      border: 2px rgb(240, 240, 240) solid;
      border-radius: 10px;
      background: white;
      margin-top: 10px;
      color: rgb(24, 152, 217);
      padding: 0 10px;
      outline: none;
    }
  }
}
</style>



