<template>
  <div id="home" v-wechat-title="$route.meta.title">
    <HomeHeader></HomeHeader>
    <HomeNav
      @transferKindValue='getKindId'
      :kindId='kindId'
    ></HomeNav>
    <HomeNewOrHot
      :NewOrHot='newOrHot'
      @transferNewOrHot='getNewOrHotValue'
    ></HomeNewOrHot>
    <ItemList
      :ItemListState='itemListState'
      :KindValue='kindValue'
      :NewOrHotValue='newOrHot'
    ></ItemList>
    <div class="underBottomNav"></div>
  </div>
</template>

<script>
import HomeHeader from './HomeHeader.vue'
import HomeNav from './HomeNav.vue'
import HomeNewOrHot from './HomeNewOrHot.vue'
import ItemList from '@/components/item_list/ItemList.vue'

export default {
  components: {
    HomeHeader,
    HomeNav,
    HomeNewOrHot,
    ItemList
  },
  data () {
    return {
      itemListState: 'kind',
      kindValue: '',
      newOrHot: 'new'
    }
  },
  methods: {
    getKindValue (kind) {
      this.kindValue = kind;
      this.newOrHot = "new";
    },
    getNewOrHotValue (NewOrHot) {
      this.newOrHot = NewOrHot;
    }
  },
  mounted () {
    this.$store.dispatch('Login')
  }
}
</script>

<style lang="scss">
#home {
  background: white;
  .underBottomNav {
    height: 50px;
    width: 100%;
  }
}
</style>



