<template>
  <div class="itemListDetailPicture">

          <div
            class="bor"
            v-for='(item,index) of pictures'
            :key="index"
          >
            <img
              v-lazy="item"
              class="picture"
            >
          </div>
  </div>
</template>

<script>
export default {
  props: {
    pictures: {
      type: Array
    }
  }
}
</script>

<style lang="scss">
.itemListDetailPicture {
  height: 80px;
  margin-top: 10px;
  .bor {
    padding: 0;
    display: inline-block;
    overflow: hidden;
    width: 28%;
    margin-right: 10px;
    .picture {
      height: 80px;
      &[lazy="loading"] {
        opacity: 0.5;
      }
    }
  }
}
</style>



