<template>
  <div class="itemBooking" @click='intoLeaveBookingMessage'>
    
  </div>
</template>

<script>
export default {
  methods: {
    intoLeaveBookingMessage () {
      this.$router.push('/leave_booing_message/' + this.$route.params.itemId)
    }
  }
}
</script>

<style lang="scss">
.itemBooking {
  width: 100%;
  background: rgb(248, 167, 87);
  color: white;
}
</style>



