<template>
  <div id="myHeader">
		<div class="bor-one"></div>
		<div class="bor-two"></div>
		<img :src="myInfo.userHead" class="userHead">
		<span class="userName"> {{ myInfo.userName }} </span>
		<span class="credit"> : {{ myInfo.credit }} </span>
  </div>
</template>

<script>
export default {
	props: {
		myInfo: {
			type: Object
		}
	}
}
</script>

<style lang="scss">
#myHeader {
  text-align: left;
  margin: 0 0 15px;
  //background: rgb(31, 197, 245);
	// background: rgb(24, 152, 217);
	background: linear-gradient(to right bottom,rgb(24, 152, 217),rgb(81, 207, 245));
  padding: 30px 30px;
  color: white;
	height: 130px;
  .userHead {
    border-radius: 50%;
    width: 60px;
    height: 60px;
    border: 2px white solid;
		float: left;
		margin-right: 20px;
		position: absolute;
  }
	.bor-one {
		width: 72px;
		height: 72px;
		border-radius: 50%;
		background: white;
		position: absolute;
		top: 24px;
		left: 24px;
		opacity: .4;
	}
	.bor-two {
		width: 90px;
		height: 90px;
		border-radius: 50%;
		background: white;
		position: absolute;
		top: 15px;
		left: 15px;
		opacity: .2;
	}
	.userName {
		font-size: 1.2em;
		display: block;
		margin-left: 90px;
	}
	.credit {
		font-size: .8em;
		margin-left: 90px;
		
	}
}
</style>



