<template>
  <div class="leaveComment" v-wechat-title="$route.meta.title">
    <textarea class="leaveCommentArea" v-model="commentValue"></textarea>
    <button class="back" @click="back">  </button>
    <button class="confirm" @click="confirm">  </button>
  </div>
</template>

<script>
export default {
  data () {
    return {
      commentValue: null
    }
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    confirm () {

    }
  }
}
</script>

<style lang="scss">
.leaveComment {
  .leaveCommentArea {
    height: 80px;
    width: 80%;
    border: 1px rgb(220, 220, 220) solid;
    margin: 20px;
    border-radius: 4px;
    color: rgb(70, 70, 70);
  }
  button {
    margin: 0 20px;
    width: 30%;
    border-radius: 4px;
    padding: 2px;
  }
  .back {
    border: 1px rgb(220, 220, 220) solid;
    background: white;
  }
  .confirm {
    background: rgb(24, 152, 217);
    color: white;
  }
}
</style>


