<template>
  <div id="releaseName">
    <label>  </label>
  	<input
      type="text"
      placeholder="(15)"
      maxlength="15"
      v-model='name'
      @input='setName'
    >
  </div>
</template>

<script>
export default {
  data () {
    return {
      name: ''
    }
  },
  methods: {
    setName (e) {
      this.$emit('transferName', this.name);
    }
  }
}
</script>

<style lang="scss">
#releaseName {
  height: 45px;
  line-height: 45px;
  border-bottom: 1px rgb(220, 220, 220) solid;
  input {
    margin-left: 15px;
    height: 30px;
    line-height: 30px;
    width: 80%;
  }
}
</style>



