<template>
  <div id="itemComments">
  	<div class="commentsHead">
  		 {{ comments.length }}
  	</div>
   	<div class="comments">
 			<div
	  		class="comment"
	  		v-for='(comment,index) of comments'
	  		v-if="index==0||show==1"
	  		:key='index'
  		>
  			<img v-lazy="comment.UserBase.avatar" class="headImage">
  			<div class="text">
  				<span class="userName"> {{ comment.UserBase.nName }} </span>
  				<span class="commentContent"> {{ comment.content }} </span>
  				<span class="commentTime"> {{ comment.createTime }} </span>
  			</div>
  		</div>
  	</div>
  	<div
  		class="showTip"
  		v-if="show==0"
  		@click="show=1"
  	>
	  	
      <i class="iconfont icon-arrowdown"></i>
	  </div>
	  <div
  		class="putTip"
  		v-if="show==1"
  		@click="show=0"
  	>
	  	
      <i class="iconfont icon-arrowup"></i>
	  </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      show: 0,
    }
  },
  props: {
    comments: {
      type: Array,
      required: true
    }
  },
}
</script>

<style lang="scss">
#itemComments {
  .commentsHead {
    color: rgb(24, 152, 217);
    padding: 4px 30px;
    border-top: 1px rgb(24, 152, 217) solid;
  }
  .comments {
    .comment {
      border-top: 1px rgb(233, 233, 233) solid;
      min-height: 55px;
      .headImage {
        border-radius: 50%;
        width: 30px;
        height: 30px;
        float: left;
        margin: 10px;
      }
      .text {
        display: inline-block;
        width: 70%;
        margin-left: 10px;
        .userName {
          width: 60%;
          font-size: 0.9em;
        }
        .commentContent {
          display: block;
          font-size: 0.8em;
          color: rgb(71, 71, 71);
        }
        .commentTime {
          float: right;
          font-size: 0.6em;
          color: rgb(71, 71, 71);
        }
      }
    }
  }
  .showTip,
  .putTip {
    font-size: .8em;
    padding: 5px 5px 10px;
    text-align: center;
    color: rgb(248, 167, 87);
    border-bottom: 1px rgb(233, 233, 233) solid;
    i {
      font-weight: 600;
      position: relative;
      top: 2px;
    }
  }
}
</style>



