<template>
  <div id="release" v-wechat-title="$route.meta.title">
    <ReleaseHeader></ReleaseHeader>
    <!--  -->
    <ReleaseName
      @transferName='getName'
    ></ReleaseName>
    <!--  -->
    <ReleaseQuantity
      @transferQuantity='getQuantity'
    ></ReleaseQuantity>
    <!--  -->
    <ReleaseIntroduction
      @transferIntroduction='getIntroduction'
    ></ReleaseIntroduction>
    <!--  -->
    <ReleasePhoto
      @transferPhotos='getPhotos'
    ></ReleasePhoto>
    <!--  -->
    <ReleaseKind
      @transferKind='getKind'
    ></ReleaseKind>
    <!--  -->
    <ReleaseMode
      @transferMode='getMode'
    ></ReleaseMode>
    <!--  -->
    <ReleasePrice
      @transferPrice='getPrice'
    ></ReleasePrice>
    <!--  -->
    <ReleaseButton
      @release='release'
    ></ReleaseButton>
  </div>
</template>

<script>
import ReleaseHeader from './ReleaseHeader.vue'
import ReleaseName from './ReleaseName.vue'
import ReleaseQuantity from './ReleaseQuantity.vue'
import ReleaseIntroduction from './ReleaseIntroduction.vue'
import ReleaseKind from './ReleaseKind.vue'
import ReleaseMode from './ReleaseMode.vue'
import ReleasePrice from './ReleasePrice.vue'
import ReleasePhoto from './ReleasePhoto.vue'
import ReleaseButton from './ReleaseButton.vue'
import { postRelease } from '@/api/article'

export default {
  components: {
    ReleaseHeader,
    ReleaseName,
    ReleaseQuantity,
    ReleaseIntroduction,
    ReleaseKind,
    ReleaseMode,
    ReleasePrice,
    ReleasePhoto,
    ReleaseButton
  },
  data () {
    return {
      item: {
        name: '',
        quantity: 1,
        introduction: '',
        kind: '',
        mail: 0,
        mailPrice: -1,
        faceToFace: 0,
        faceArea: '',
        originalPrice: 0,
        price: '',
        photoFiles: []
      }
    }
  },
  methods: {
    release () {
      var item = this.item
      if (item.name && item.kind
        && (item.mail || item.faceToFace)
        && item.price && item.photoFiles[0]) {
        var releaseItem = {
          "name": item.name,
          "count": item.quantity,
          "desc": item.introduction,
          "catId": item.kind,
          // "mail": item.mail,
          // "mailPrice": item.mailPrice,
          // "faceToFace": item.faceToFace,
          // "faceArea": item.faceArea,
          // "originalPrice": item.originalPrice,
          "price": item.price,
          "images": item.photoFiles
        }
        console.log(releaseItem)
        postRelease(releaseItem).then(response => {
          const data = response.data
          if (data.code == 200) {
            alert('')
          } else {
            alert('')
          }
        }).catch(error => {
          console.error(error)
        })
      }
      else {
        if (!item.photoFiles[0]) {
          alert("")
        }
        else if (!(item.mail || item.faceToFace)) {
          alert("")
        } else {
          alert("")
        }
      }
    },
    getName (name) {
      this.item.name = name
    },
    getQuantity (quantity) {
      this.item.quantity = quantity
    },
    getIntroduction (introduction) {
      this.item.introduction = introduction
    },
    getKind (kind) {
      this.item.kind = kind;
    },
    getMode (mode) {
      this.item.mail = mode.mail
      this.item.mailPrice = mode.mailPrice
      this.item.faceToFace = mode.faceToFace
      this.item.faceArea = mode.faceArea
    },
    getOriginalPrice (originalPrice) {
      this.item.originalPrice = originalPrice
    },
    getPrice (price) {
      this.item.price = price
    },
    getPhotos (photos) {
      this.item.photoFiles = photos
    }
  }
}
</script>

<style lang="scss">
#release {
  color: rgb(70, 70, 70);
  font-size: 0.9em;
}
</style>



