<template>
  <div id="releaseButton">
    <button
      @click='release'
    >
      
    </button>
  </div>
</template>

<script>
export default {
  data () {
    return {

    }
  },
  methods: {
    release () {
      this.$emit('release')
    }
  }
}
</script>

<style lang="scss">
#releaseButton {
  button {
    border: none;
    outline: none;
    background: rgb(24, 152, 217);
    padding: 6px 40px;
    color: white;
    border-radius: 10px;
    display: block;
    margin: 10px auto 60px;
    clear: both;
  }
}
</style>



