<template>
  <div id="releaseIntroduction">
  	<textarea
      rows="2"
      type="text"
      placeholder=""
      maxlength="40"
      v-model='introduction'
      @input='setIntroduction'
    ></textarea>
  </div>
</template>

<script>
export default {
  data(){
    return{
      introduction: ''
    }
  },
  methods: {
    setIntroduction (e) {
      this.$emit('transferIntroduction', this.introduction);
    }
  }
}
</script>

<style lang="scss">
#releaseIntroduction {
  textarea {
    width: 100%;
    padding: 10px;
  }
}
</style>



