<template>
  <div class="container" id="bottomNav" v-show="show">
    <div class="row">
      <div
        class="col"
        v-for='(nav,index) of navs'
        :key="index"
      >
        <router-link
          :to='nav.to'
          :class='{"active":(activeIndex==index)}'
        >
          <i
            class="iconfont"
            :class="nav.icon"
          ></i>
          {{ nav.name }}
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      show: 1,
      navs: [
        {
          name: '',
          to: '/',
          icon: 'icon-home'
        }, {
          name: '',
          to: '/release',
          icon: 'icon-release'
        }, {
          name: '',
          to: '/message',
          icon: 'icon-message'
        }, {
          name: '',
          to: '/my',
          icon: 'icon-my'
        }
      ],
      activeIndex: null
    }
  },
  mounted () {
    var name = this.$route.name
    if (name == 'Home') {
      this.activeIndex = 0
      this.show = 1
    }
    else if (name == 'Release') {
      this.activeIndex = 1
      this.show = 1
    }
    else if (name == 'Message') {
      this.activeIndex = 2
      this.show = 1
    }
    else if (name == 'My') {
      this.activeIndex = 3
      this.show = 1
    }
    else {
      this.show = 0
    }
  },
  watch: {
    '$route' (to, from) {
      var name = to.name
      if (name == 'Home') {
        this.activeIndex = 0
        this.show = 1
      }
      else if (name == 'Release') {
        this.activeIndex = 1
        this.show = 1
      }
      else if (name == 'Message') {
        this.activeIndex = 2
        this.show = 1
      }
      else if (name == 'My') {
        this.activeIndex = 3
        this.show = 1
      }
      else {
        this.show = 0
      }
    }
  }
}
</script>

<style lang="scss">
#bottomNav {
  height: 50px;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  background: rgb(248, 247, 245);
  box-shadow: 0.5px -0.5px 1px rgb(222, 222, 222);
  z-index: 999;
  a {
    color: rgb(94, 94, 94);
    font-size: 0.8em;
    text-decoration: none;
    &.active {
      color: rgb(24, 152, 217);
    }
    i {
      font-size: 26px;
      display: block;
      margin-top: -2px;
      margin-bottom: -10px;
    }
  }
}
</style>



