<template>
  <div id="releaseQuantity">
    <label>  </label>
  	<input
      type="number"
      v-model='quantity'
      oninput="if(value.length>3)value=value.slice(0,3)"
            onKeypress="return (/[\d]/.test(String.fromCharCode(event.keyCode)))"
      @input='setQuantity'
    >
  </div>
</template>

<script>
export default {
  data () {
    return {
      quantity: 1
    }
  },
  methods: {
    setQuantity (e) {
      this.$emit('transferQuantity', this.quantity);
    }
  }
}
</script>

<style lang="scss">
#releaseQuantity {
  height: 45px;
  line-height: 45px;
  border-bottom: 1px rgb(220, 220, 220) solid;
  input {
    margin-left: 15px;
    height: 30px;
    line-height: 30px;
    width: 80%;
    color: rgb(24, 152, 217);
  }
}
</style>



