<template>
  <div
    class="messageChatItems"
    @click='intoChat(item.userId)'
  >
    <span
      class="newMessage"
      :class='{"hidden":(!item.newMessage)}'
    >
      {{ item.newMessage }}
    </span>
    <img v-lazy="item.userHead" class="userHead">
    <span class="userName">
      {{ item.userName }}
    </span>
    <span class="lastTime">
      {{ item.lastTime }}
    </span>
    <span class="lastMessage">
      {{ item.lastMessage }}
    </span>
  </div>
</template>

<script>
export default {
  props: {
    item: {
      type: Object
    }
  },
  methods: {
    intoChat (user) {
      this.$router.push({ name: 'Chat', params: { userName: user } })
    }
  }
}
</script>

<style lang="scss">
.messageChatItems {
  height: 60px;
  border-bottom: 1px rgb(240, 240, 240) solid;
  .userHead {
    border-radius: 5px;
    width: 40px;
    height: 40px;
    margin: 10px 0;
    float: left;
  }
  .userName {
    font-size: 1em;
    margin: 10px 10px 0;
    display: inline-block;
  }
  .lastTime {
    color: rgb(150, 150, 150);
    font-size: 0.7em;
    float: right;
    margin: 10px 30px 0 0;
  }
  .lastMessage {
    color: rgb(150, 150, 150);
    font-size: 0.8em;
    display: block;
    width: 70%;
    height: 20px;
    overflow: hidden;
    position: relative;
    left: 10px;
  }
  .newMessage {
    display: inline-block;
    min-width: 20px;
    width: auto;
    height: 20px;
    line-height: 16px;
    border: 1px white solid;
    border-radius: 50%;
    float: left;
    color: white;
    background: rgb(239, 80, 43);
    padding: 1px 1px;
    font-size: 13px;
    text-align: center;
    z-index: 10;
    position: relative;
    left: 47px;
    &.hidden {
      visibility: hidden;
    }
  }
}
</style>



