<template>
  <div id="myRelease" v-wechat-title="$route.meta.title">
    <div
      class="myReleaseItem"
      v-for='(item, index) of items'
      :key='index'
    >
      <img v-lazy="item.images" class="picture">
      <div class='left'>
        <span class="name"> {{ item.name }} </span>
        <span class="releaseTime"> {{ item.tmCreated }} </span>
        <span class="views"> {{ item.views }} </span>
        <!-- <span class="bookings"> {{ item.bookings }} </span> -->
      </div>
      <div class="right">
        <span class="price"> {{ item.price }} </span>
        <span class="originalPrice"><del> {{ item.price }} </del></span>
        <button
          class='edit'
          @click='intoEdit(item.id)'
        >  </button>
        <button
          class='view'
          @click='intoBookingMessages(item.id)'
        >  </button>
      </div>
    </div>
  </div>
</template>

<script>
import tfTimestamp from '@/utils/tfTimestamp.js'
import { myRelease } from '@/api/article'

export default {
  data () {
    return {
      items: []
    }
  },
  mounted () {
    myRelease().then(response => {
      const data = response.data
      if (data.code == 200) {
        for (var i in data.content) {
          data.content[i].tmCreated = tfTimestamp(data.content[i].tmCreated)
        }
        this.items = data.content
      } else {
        console.error(data.message)
      }
    })
  },
  methods: {
    intoEdit (itemId) {
      this.$router.push({ name: 'EditItem', params: { id: itemId } })
    },
    intoBookingMessages (itemId) {
      this.$router.push({ name: 'MessageBookingItemMessages', params: { id: itemId } })
    }
  }
}
</script>

<style lang="scss">
#myRelease {
  .myReleaseItem {
    height: 110px;
    background: rgb(250, 250, 250);
    text-align: left;
    padding: 5px 10px;
    font-size: 0.9em;
    border-bottom: 2px rgb(240, 240, 240) solid;
    .picture {
      width: 80px;
      height: 80px;
      float: left;
    }
    .left {
      display: inline-block;
      width: 40%;
      padding: 0 10px;
      .name {
        color: rgb(42, 65, 75);
        display: block;
      }
      .releaseTime,
      .views,
      .bookings {
        font-size: 0.9em;
        color: rgb(120, 120, 120);
        display: block;
      }
    }
    .right {
      float: right;
      display: inline-block;
      width: 30%;
      text-align: center;
      .price {
        display: block;
        color: rgb(95, 95, 95);
      }
      .originalPrice {
        display: block;
        color: rgb(150, 150, 150);
      }
      .edit,
      .view {
        font-size: 0.9em;
        border: 2px rgb(240, 240, 240) solid;
        border-radius: 18px;
        background: white;
        color: rgb(24, 152, 217);
        padding: 2px 10px;
        outline: none;
      }
    }
  }
}
</style>



