import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/home/Home'
import Release from '@/views/release/Release'
import Message from '@/views/message/Message'
import MessageBooking from '@/views/message/MessageBooking'
import MessageBookingItemMessages from '@/views/message/MessageBookingItemMessages'
import MessageNotice from '@/views/message/MessageNotice'
import Chat from '@/views/chat/Chat'
import My from '@/views/my/My'
import MyRelease from '@/views/my/MyRelease'
import MyBooking from '@/views/my/MyBooking'
import MySold from '@/views/my/MySold'
import MyBought from '@/views/my/MyBought'
import MyCollection from '@/views/my/MyCollection'
import Search from '@/views/search/Search'
import SearchResult from '@/views/search_result/SearchResult'
import Item from '@/views/item/Item'
import EditItem from '@/views/edit_item/EditItem'
import LeaveComment from '@/views/leave_comment/LeaveComment'
import LeaveBookingMessage from '@/views/leave_booking_message/LeaveBookingMessage'
import Customer from '@/views/customer/Customer'
import Feedback from '@/views/feedback/Feedback.vue'
import AboutUs from '@/views/about_us/AboutUs.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      meta: {
        title: ''
      },
      component: Home
    },
    {
      path: '/release',
      name: 'Release',
      meta: {
        title: ''
      },
      component: Release
    },
    {
      path: '/message',
      name: 'Message',
      meta: {
        title: ''
      },
      component: Message
    },
    {
      path: '/message/booking',
      name: 'MessageBooking',
      meta: {
        title: ''
      },
      component: MessageBooking
    },
    {
      path: '/message/booking/item_messages',
      name: 'MessageBookingItemMessages',
      component: MessageBookingItemMessages
    },
    {
      path: '/message/notice',
      name: 'MessageNotice',
      meta: {
        title: ''
      },
      component: MessageNotice
    },
    {
      path: '/message/chat',
      name: 'Chat',
      component: Chat
    },
    {
      path: '/my',
      name: 'My',
      meta: {
        title: ''
      },
      component: My
    },
    {
      path: '/my/my_release',
      name: 'MyRelease',
      meta: {
        title: ''
      },
      component: MyRelease
    },
    {
      path: '/my/my_booking',
      name: 'MyBooking',
      meta: {
        title: ''
      },
      component: MyBooking
    },
    {
      path: '/my/my_sold',
      name: 'MySold',
      meta: {
        title: ''
      },
      component: MySold
    },
    {
      path: '/my/my_bought',
      name: 'MyBought',
      meta: {
        title: ''
      },
      component: MyBought
    },
    {
      path: '/my/my_collection',
      name: 'MyCollection',
      meta: {
        title: ''
      },
      component: MyCollection
    },
    {
      path: '/search',
      name: 'Search',
      component: Search
    },
    {
      path: '/search_result',
      name: 'SearchResult',
      component: SearchResult
    },
    {
      path: '/item/:itemId',
      name: 'Item',
      meta: {
        title: ''
      },
      component: Item
    },
    {
      path: '/edit_item',
      name: 'EditItem',
      meta: {
        title: ''
      },
      component: EditItem
    },
    {
      path: '/leave_comment/:itemId',
      name: 'LeaveComment',
      meta: {
        title: ''
      },
      component: LeaveComment
    },
    {
      path: '/leave_booing_message/:itemId',
      name: 'LeaveBookingMessage',
      meta: {
        title: ''
      },
      component: LeaveBookingMessage
    },
    {
      path: '/customer/:id',
      name: 'Customer',
      component: Customer
    },
    {
      path: '/feedback',
      name: 'Feedback',
      meta: {
        title: ''
      },
      component: Feedback
    },
    {
      path: '/about_us',
      name: 'AboutUs',
      meta: {
        title: ''
      },
      component: AboutUs
    }
  ]
})



