<template>
  <div class="itemLike">
    <i
      class="iconfont icon-like"
      v-if='isLike==0'
      @click="setLike"
    ></i>
    <i
      class="iconfont icon-like2"
      v-if='isLike==1'
      @click="removeLike"
    ></i>
    <span class="likeTotal"> {{ like.total }} </span>
  </div>
</template>

<script>
import { postLike, cancelLike } from '@/api/article'

export default {
  props: {
    like: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      isLike: this.like.own
    }
  },
  methods: {
    setLike (e) {
      postLike(this.$route.params.itemId).then(response => {
        const data = response.data
        if (data.code == 200) {
          this.isLike = 1
        }
      })
    },
    removeLike (e) {
      cancelLike(this.$route.params.itemId).then(response => {
        const data = response.data
        if (data.code == 200) {
          this.isLike = 0
        }
      })
    }
  }
}
</script>

<style lang="scss">
.itemLike {
  background: rgb(248, 247, 245);
  color: rgb(110, 110, 110);
  i {
    font-size: 1.5em;
    &.icon-like2 {
      color: rgb(250, 117, 76);
    }
  }
}
</style>



