<template>
  <div
    class="ItemListDetail"
    @click='intoItem(item.detail.id)'
  >
    <div class="head">
      <!---->
      <img class="userHead" v-lazy="item.user.headImage">
      <div class="text">
        <!---->
        <p class="name"> {{ item.detail.name }}</p>
        <!---->
        <span class="userName">{{ item.user.name }}</span>
        <!---->
        <span class="releaseTime">{{ item.detail.releaseTime }}</span>
      </div>
      <!---->
      <p class="price-div">
        <span class="price">
          <span class="rmbSymbol"></span> {{ item.detail.price }}
        </span>
        <span class="originalPrice">
           {{ item.detail.originalPrice }}
        </span>
      </p>
    </div>
	  <div class="body">
      <!---->
      <ItemListDetailPicture
        :pictures='item.detail.images'
      ></ItemListDetailPicture>
	    <p class="introduction"> {{ item.detail.introduction }} </p>
	    <p class="tip"> -> </p>
	  </div>
	</div>
</template>

<script>
import ItemListDetailPicture from './ItemListDetailPicture.vue'
export default {
  components:{
    ItemListDetailPicture
  },
  props: {
    item: {
      type: Object,
      required: true
    }
  },
  methods: {
    intoItem (id) {
      this.$router.push('/item/' + id)
    }
  }
}
</script>

<style lang="scss">
.ItemListDetail {
  height: 180px;
  color: rgb(71, 71, 71);
  border-bottom: 1px solid rgb(240, 240, 240);
  text-align: left;
  .head {
    height: 50px;
    width: 100%;
    padding: 5px 0 0 10px;
    .userHead {
      height: 30px;
      width: 30px;
      border-radius: 50%;
      float: left;
    }
    .text {
      margin-left: 20px;
      width: 60%;
      display: inline-block;
      .name {
        margin-bottom: -5px;
        font-size: 1em;
      }
      .userName {
        font-size: 0.8em;
      }
      .releaseTime {
        font-size: 0.6em;
      }
    }
    .price-div {
      display: inline-block;
      width: 22%;
      margin-bottom: 0;
      .price {
        color: rgb(239, 73, 43);
        margin: 0;
        display: block;
        font-size: 1.2em;
        .rmbSymbol {
          font-size: 0.6em;
        }
      }
      .originalPrice {
        text-decoration: line-through;
        font-size: 0.6em;
        position: relative;
        top: -8px;
        left: 3px;
      }
    }
  }
  .body {
    height: 130px;
    margin-left: 60px;
    .introduction {
      margin-bottom: 5px;
      font-size: 0.9em;
      width: 80%;
      height: 1.2em;
      overflow: hidden;
    }
    .tip {
      font-size: 0.6em;
      color: rgb(130, 131, 133);
    }
  }
}
</style>



