<template>
  <div id="releasePrice">
    <div class="container">
      <div class="row">
        <div class="col">
          <label>  </label>
          <input
            type="number"
            oninput="if(value.length>5)value=value.slice(0,5)"
            onKeypress="return (/[\d]/.test(String.fromCharCode(event.keyCode)))"
            v-model='originalPrice'
            @input='setOriginalPrice'
          />
        </div>
        <div class="col">
          <label>  </label>
          <input
            type="number"
            oninput="if(value.length>5)value=value.slice(0,5)"
            onKeypress="return (/[\d]/.test(String.fromCharCode(event.keyCode)))"
            v-model='price'
            @input='setPrice'
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      originalPrice: '',
      price: ''
    }
  },
  methods: {
    setOriginalPrice (e) {
      this.$emit('transferOriginalPrice', this.originalPrice);
    },
    setPrice (e) {
      this.$emit('transferPrice', this.price);
    }
  }
}
</script>

<style lang="scss">
#releasePrice {
  input {
    text-align: center;
    width: 60%;
    border: none;
    border-bottom: 1px rgb(220, 220, 220) solid;
  }
}
</style>



