<template>
  <div id='releasePhoto'>
    <div class="header">
      <span class="title">
        
      </span>
      <span class="tip">
        
      </span>
    </div>
    <div class="container">
      <div class="row">
        <div
          class="col-3"
          v-for='(photoSrc,index) of photosSrcs'
          :key="index"
        >
          <img
            v-lazy="photoSrc"
            class="photo"
            @touchstart="removePhoto(index)"
            @touchmove="cancelRemovePhoto"
            @touchend="cancelRemovePhoto"
          >
        </div>
        <div class="col-3">
          <span
            class="choosePhoto"
            v-if="!photosSrcs[2]"
          >
            <i class="iconfont icon-setPhoto" ></i>
          </span>
          <input
            type="file"
            id="choosePhoto"
            @change='putPhoto'
            accept="image/*"
            v-if="!photosSrcs[2]"
          >
        </div>
      </div>
    </div>
    <canvas id="canvas"></canvas>
  </div>
</template>

<script>
var timeOutEvent

export default {
  data () {
    return {
      photosSrcs: [],
      photoFiles: []
    }
  },
  methods: {
    putPhoto (e) {
      var fileChooser = document.getElementById("choosePhoto");
      var photo = fileChooser.value;
      var src = window.navigator.userAgent.indexOf("Chrome") >= 1 ||
        window.navigator.userAgent.indexOf("Safari") >= 1 ?
        window.webkitURL.createObjectURL(fileChooser.files[0]) :
        window.URL.createObjectURL(fileChooser.files[0])
      this.photosSrcs.push(src);

      const maxSize = 200 * 1024; // 200k
      var file = fileChooser.files[0];

      var reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = (event) => {
        var result = event.target.result;

        if (file.size > maxSize) { //
          var img = new Image();
          img.src = result;
          img.onload = () => {
            var canvas = document.getElementById("canvas");
            var ctx = canvas.getContext('2d');
            canvas.width = img.width;
            canvas.height = img.height;

            ctx.drawImage(img, 0, 0, canvas.width, canvas.height);
            result = canvas.toDataURL('image/jpeg', 0.1); // 0.1
            this.photoFiles.push(result);
          }
        }
        else {
          this.photoFiles.push(result);
        }
      }
      setTimeout(() => this.$emit('transferPhotos', this.photoFiles), 300);
    },
    removePhoto (index) {
      timeOutEvent = setTimeout(() => {
        var con = confirm("")
        if (con == true) {
          this.photosSrcs.splice(index, 1)
          this.photoFiles.splice(index, 1)
        }
      }, 2000)
    },
    cancelRemovePhoto () {
      clearTimeout(timeOutEvent);
      timeOutEvent = 0;
    }
  }
}
</script>

<style lang="scss">
#releasePhoto {
  margin-top: 20px;
  text-align: left;
  .header {
    margin-bottom: 5px;
    .title {
      margin-left: 4%;
      font-size: 1.1em;
    }
    .tip {
      color: rgb(180, 180, 180);
      font-size: 0.6em;
      margin-left: 20px;
    }
  }
  .container {
    margin-left: 10px;
    .row {
      height: 75px;
    }
    .col-3 {
      height: 75px;
      overflow: hidden;
      min-width: 75px;
      padding: 0;
      .photo {
        float: left;
        min-height: 75px;
        width: 75px;
      }
    }
    .choosePhoto {
      height: 75px;
      width: 75px;
      border: 2px rgb(160, 160, 160) solid;
      display: inline-block;
      float: left;
      line-height: 75px;
      text-align: center;
      i {
        font-size: 60px;
        color: rgb(160, 160, 160);
        font-weight: 500;
      }
    }
    #choosePhoto {
      width: 75px;
      height: 75px;
      border: 1px black solid;
      display: block;
      float: left;
      position: relative;
      left: -75px;
      margin-right: -75px;
      opacity: 0;
    }
  }
  #canvas {
    opacity: 0;
    position: absolute;
    top: 0;
    width: 10px;
    height: 10px;
  }
}
</style>



