<template>
  <div id="itemBottom">
    <div class="container">
      <div class="row">
        <div class="col">
          <ItemLike
            :like='like'
          ></ItemLike>
        </div>
        <div class="col">
          <ItemLeaveComment></ItemLeaveComment>
        </div>
        <div class="col">
          <ItemBooking></ItemBooking>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ItemLike from '@/components/item_like/ItemLike.vue'
import ItemLeaveComment from '@/components/item_leave_comment/ItemLeaveComment.vue'
import ItemBooking from '@/components/item_booking/ItemBooking.vue'

export default {
  components: {
    ItemLike,
    ItemLeaveComment,
    ItemBooking
  },
  props: {
    like: {
      type: Object,
      required: true
    }
  },
  methods: {

  }
}
</script>

<style lang="scss">
#itemBottom {
  text-align: center;
  height: 40px;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: 0.5px -0.5px 1px rgb(222, 222, 222);
  line-height: 40px;
  font-size: 0.8em;
  .col {
    padding: 0;
  }
}
</style>



