<template>
  <div id="myBought" v-wechat-title="$route.meta.title">
    <div
      class="myBoughtItem"
      v-for='(item, index) of items'
      :key='index'
    >
      <img v-lazy="item.images[0]" class="picture">
      <div class='left'>
        <span class="name"> {{ item.name }} </span>
      </div>
      <div class="right">
        <span class="price"> {{ item.price }} </span>
        <button @click='intoItem(item.id)'>  </button>
      </div>
    </div>
  </div>
</template>

<script>
import { myCollection } from '@/api/article.js'

export default {
  data () {
    return {
      items: []
    }
  },
  mounted () {
    myCollection().then(response => {
      const data = response.data
      if (data.code == 200) {
        this.items = data.content
      }
    })
  },
  methods: {
    intoItem (id) {
      this.$router.push('/item/' + id)
    }
  }
}
</script>

<style lang="scss">
#myBought {
  .myBoughtItem {
    height: 100px;
    background: rgb(250, 250, 250);
    text-align: left;
    padding: 5px 10px;
    font-size: 0.9em;
    border-bottom: 2px rgb(240, 240, 240) solid;
    .picture {
      width: 80px;
      height: 80px;
      float: left;
    }
    .left {
      display: inline-block;
      width: 40%;
      padding: 0 10px;
      .name {
        color: rgb(42, 65, 75);
        display: block;
      }
      .boughtTime,
      .buyer {
        font-size: 0.9em;
        color: rgb(120, 120, 120);
        display: block;
      }
    }
    .right {
      float: right;
      display: inline-block;
      width: 30%;
      text-align: center;
      .price {
        display: block;
        color: rgb(95, 95, 95);
      }
      .originalPrice {
        display: block;
        color: rgb(150, 150, 150);
      }
      button {
        font-size: 0.9em;
        border: 2px rgb(240, 240, 240) solid;
        border-radius: 10px;
        background: white;
        color: rgb(24, 152, 217);
        padding: 0 10px;
        outline: none;
      }
    }
  }
}
</style>



