<template>
  <div id="releaseKind">
    <label>  </label>
    <span
      class="kind"
      v-for="(item,index) of kinds"
      :key="index"
      @click="setKind(item.id)"
      :class="{'active':(item.id==kindId)}"
    >
      {{ item.name }}
    </span>
  </div>
</template>

<script>
import { getKinds } from '@/api/article'

export default {
  data () {
    return {
      kindId: null,
      kinds: []
    }
  },
  methods: {
    setKind (kindId) {
      this.kindId = kindId
      this.$emit('transferKind', kindId);
    }
  },
  mounted () {
    getKinds().then(response => {
      const data = response.data
      if (data.code == 200) {
        this.kinds = data.content
      } else {
        console.error(data.message)
      }
    })
  }
}
</script>

<style lang="scss">
#releaseKind {
  padding: 0 15px 15px;
  label {
    display: block;
    text-align: left;
  }
  .kind {
    display: inline-block;
    padding: 2px 15px;
    border-radius: 18px;
    margin-right: 10px;
    margin-bottom: 10px;
    background: rgb(240, 240, 240);
    &.active {
      color: white;
      background: rgb(24, 152, 217);
    }
  }
}
</style>



