<template>
  <div id="myContact">
    <div class="header">
      
    </div>
    <div class="container">
      <div class="row">
        <div
          class="col-3"
          v-for='(item,index) of items'
   		    :key='index'
          @click='intoItem(item.name)'
          :class='{"bor-right":(index!=3)}'
        >
          <i class="iconfont" :class="item.icon"></i>
          <span class="text">{{ item.text }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      items: [
        {
          text: '',
          icon: 'icon-customerService'
        }, {
          text: '',
          icon: 'icon-feedback',
          name: 'Feedback'
        }, {
          text: '',
          icon: 'icon-aboutUs',
          name: 'AboutUs'
        }
      ]
    }
  },
  methods: {
    intoItem (itemName) {
      this.$router.push({ name: itemName, params: { userId: "123" } })
    }
  }
}
</script>

<style lang="scss">
#myContact {
  background: white;
  margin-top: 15px;
  margin-bottom: 50px;
  .header {
    text-align: left;
    padding: 10px 15px;
    border-bottom: 2px rgb(245, 245, 245) solid;
  }
  .col-3 {
    width: 100%;
    text-align: center;
    height: 100px;
    line-height: 50px;
    &.bor-right {
      border-right: 2px rgb(245, 245, 245) solid;
    }
    i {
      display: block;
      color: rgb(24, 152, 217);
      font-size: 2em;
      margin-top: 14px;
      margin-bottom: -15px;
    }
    .text {
      font-size: 0.7em;
      color: rgb(100, 100, 100);
    }
  }
}
</style>



