<template>
  <div id="homeNewOrHot" class="container" >
  	<div class="row">
  		<div
        class="col"
        :class='{"active":(NewOrHot=="new")}'
        @click='setNewOrHot("new")'
      >
  			
  		</div>
  		<div
        class="col"
        :class='{"active":(NewOrHot=="hot")}'
        @click='setNewOrHot("hot")'
      >
  			
      </div>
  	</div>
  </div>
</template>

<script>
export default {
  props: {
    NewOrHot: {
      type: String,
      required: true
    }
  },
  methods: {
    setNewOrHot (NewOrHot) {
      this.$emit('transferNewOrHot', NewOrHot);
    }
  }
}
</script>

<style lang="scss">
#homeNewOrHot {
  border-top: 2px rgb(240, 240, 240) solid;
  border-bottom: 2px rgb(240, 240, 240) solid;
  line-height: 30px;
  font-size: 1em;
  .col {
    height: 30px;
    padding: 0;
    color: rgb(120, 120, 120);
    &.active {
      color: rgb(24, 152, 217);
      border-bottom: 2px rgb(24, 152, 217) solid;
      font-weight: 500;
    }
  }
}
</style>


