<template>
  <div
    id='itemImages'
    class="images"
    @touchstart='touchStart'
    @touchend='touchEnd'
  >
    <div class="carousel">
      <!---->
      <ol class="carousel-indicators">
        <li
          v-for='(image,index) of images'
          :key="index"
          :class="{'active':(activeIndex==index)}"
          @click='activeIndex=index'
        ></li>
      </ol>
      <!---->
      <div
        v-for='(image,index) of images'
        :key="index"
        class="carousel-item"
        :class="{'active':(activeIndex==index)}"
      >
        <img v-lazy="image.path">
      </div>
      <!---->
      <a
        class="carousel-control-prev"
        @click='activeIndex-=1'
        v-if='activeIndex'
      >
        <span class="carousel-control-prev-icon"></span>
      </a>
      <a
        class="carousel-control-next"
        @click='activeIndex+=1'
        v-if='images.length-1!=activeIndex'
      >
        <span class="carousel-control-next-icon"></span>
      </a>
    </div>
  </div>
</template>

<script>
var startX = 0;
var endX = 0;

export default {
  data () {
    return {
      activeIndex: 0
    }
  },
  props: {
    images: {
      type: Array,
      required: true
    }
  },
  methods: {
    touchStart (e) {
      startX = e.changedTouches[0].screenX;
    },
    touchEnd (e) {
      endX = e.changedTouches[0].screenX;
      var moveX = endX - startX;
      if (moveX > 0 && this.activeIndex > 0)
        this.activeIndex -= 1;
      if (moveX < 0 && this.activeIndex < this.images.length - 1)
        this.activeIndex += 1;
    }
  }
}
</script>

<style lang="scss">
#itemImages {
  height: 300px;
  line-height: 300px;
  background: rgb(244, 244, 244);
  li {
    background: white;
    &.active {
      background: rgb(24, 152, 217);
    }
  }
  img {
    max-height: 320px;
    width: 100%;
    display: inline-block;
    margin: 0;
  }
}
</style>



