<template>
  <div id='searchHot'>
  	<span class="title">
  		
  	</span>
  	<div class="hotList">
  		<span
  			class="hotItem"
  			v-for='(hotItem,index) of hotItems'
        :key="index"
        @click='clickItem(hotItem)'
  		>
  			{{ hotItem }}
  		</span>
  	</div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      hotItems: ['', '', '', '', '', '', '', '']
    }
  },
  methods: {
    clickItem (text) {
      //
      var value = text;
      if (!localStorage.historyItem) {
        localStorage.historyItem = value;
      }
      else {
        var Array = [];
        Array = localStorage.historyItem.split(',');
        //
        for (var index = 0; index < Array.length; index++) {
          if (value == Array[index]) Array.splice(index, 1);
        }
        Array.unshift(value);
        localStorage.historyItem = Array;
      }
      //text
      this.$emit('transferInputValue', text);
      //
      this.$emit('search');
    }
  }
}
</script>

<style lang="scss">
#searchHot {
  margin-top: 10px;
  text-align: left;
  .title {
    margin: 15px;
  }
  .hotList {
    margin-top: 10px;
    width: 80%;
    .hotItem {
      display: inline-block;
      padding: 4px 12px;
      border-radius: 18px;
      background: rgb(245, 245, 245);
      font-size: 0.9em;
      margin-bottom: 8px;
      margin-left: 18px;
    }
  }
}
</style>



