<template>
  <div id="messageBooking" v-wechat-title="$route.meta.title">
    <MessageBookingItem
      v-for='(item,index) of items'
      :key='index'
      :item='item'
    ></MessageBookingItem>
  </div>
</template>

<script>
import MessageBookingItem from './MessageBookingItem.vue'
import { myRelease } from '@/api/article.js'

export default {
  components: {
    MessageBookingItem
  },
  data () {
    return {
      items: []
    }
  },
  mounted () {
    myRelease().then(response => {
      const data = response.data
      if (data.code == 200) {
        this.items = data.content
      } else {
        alert('')
        console.error(data.message)
      }
    })
  }
}
</script>

<style lang="scss">

</style>



