<template>
  <div id="itemUser">
    <img
      :src="user.headImage"
      class="headImage"
      @click='intoUser(user.id)'
    >
    <span class="userName"> {{ user.name }} </span><br>
    <span class="credit"> : {{ user.credit }} </span><br>
    <span class="lastLoginTime"> : {{ user.lastLoginTime }} </span>
  </div>
</template>

<script>
export default {
  data () {
    return {

    }
  },
  props: {
    user: {
      type: Object,
      required: true
    }
  },
  methods: {
    intoUser (userId) {
      this.$router.push('/customer/' + userId)
    }
  }
}
</script>

<style lang="scss">
#itemUser {
  text-align: left;
  padding-bottom: 4px;
  height: 80px;
  background: rgb(81, 207, 245);
  box-shadow: 0px -5px 15px rgb(210, 210, 210);
  margin-bottom: 40px;
  line-height: 1.1em;
  .headImage {
    width: 70px;
    height: 70px;
    border-radius: 50%;
    display: inline-block;
    padding: 10px;
    float: left;
    margin: 0px 20px;
  }
  .userName {
    display: inline-block;
    margin-top: 10px;
    font-size: 1em;
  }
  .credit,
  .lastLoginTime {
    font-size: 0.6em;
    color: rgb(100, 100, 100);
  }
}
</style>



