<template>
  <div id="itemContent">
  	<span class="name"> {{ detail.name }} </span>
    <ItemCollect
      :itemIsColl='detail.isColl'
    ></ItemCollect>
  	<span class="introduction"> {{ detail.introduction }} </span>
  	<span class="price"> <span class="CNY"></span> {{ detail.price }} </span>
  	<span class="originalPrice"> <del> {{ detail.originalPrice }} </del> </span>
  	<span class="releaseTime"> {{ detail.releaseTime }} </span>
  	<span class="views"> {{ detail.views }} </span>
    <div class="mode">
  		<span>
  			<i class="iconfont icon-faceToFace"></i>
  			
  		</span>
  		<span>
  			<i class="iconfont icon-mail"></i>
	  		
	  	</span>
  	</div>
  </div>
</template>

<script>
import ItemCollect from '@/components/item_collect/ItemCollect.vue'

export default {
  components:{
    ItemCollect
  },
  props: {
    detail: {
      type: Object,
      required: true
    }
  },
}
</script>

<style lang="scss">
#itemContent {
  padding: 8px 10px 0;
  box-shadow: 0px -5px 15px rgb(210, 210, 210);
  span {
    display: block;
  }
  .name {
    font-size: 1em;
  }
  .introduction {
    font-size: 0.8em;
    color: rgb(71, 71, 71);
    display: inline;
  }
  .price {
    margin-top: 6px;
    font-size: 1.2em;
    color: rgb(239, 72, 42);
    text-shadow: 0px 0px 20px rgb(252, 222, 63);
    .CNY {
      display: inline;
      font-size: 0.7em;
    }
  }
  .originalPrice {
    color: rgb(150, 150, 150);
    font-size: 0.8em;
    margin-left: 6px;
  }
  .releaseTime,
  .views {
    margin-top: 10px;
    font-size: 0.9em;
    color: rgb(110, 110, 110);
    display: inline-block;
    width: 48%;
    border-bottom: 1px rgb(220, 220, 220) solid;
    text-align: center;
  }
  .views {
    margin-left: -2%;
  }
  .mode {
    padding: 5px 10px;
    color: rgb(71, 71, 71);
    span {
      color: rgb(24, 152, 217);
      display: inline-block;
      font-size: 0.9em;
      margin-right: 20px;
    }
  }
}
</style>



