<template>
  <div id="chat" v-wechat-title="$route.params.userName">
    <ChatContent
      :messages='messages'
      :userHead='userInfo.userHead'
      :myHead='myHead'
    ></ChatContent>
    <ChatInput
      @transferInputValue='getInputValue'
    ></ChatInput>
    <Background></Background>
  </div>
</template>

<script>
import ChatContent from './ChatContent.vue'
import ChatInput from './ChatInput.vue'
import Background from '@/components/background/Background.vue'
import { mapState } from 'vuex'

export default {
  components: {
    ChatContent,
    ChatInput,
    Background
  },
  data () {
    return {
      inputValue: '',
      userInfo: {
        userHead: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1517329853011&di=84d8b5995adb155dec089d1143d4fa65&imgtype=jpg&src=http%3A%2F%2Fimg3.imgtn.bdimg.com%2Fit%2Fu%3D3806550893%2C2239456505%26fm%3D214%26gp%3D0.jpg'
      },
      messages: [ //owner: 0-,1-
        { content: '', time: '13:00', owner: 0 },
        { content: '', time: '14:00', owner: 1 },
        { content: '', time: '15:00', owner: 0 },
        { content: '80', time: '16:00', owner: 0 },
        { content: 'USA', time: '17:00', owner: 1 },
      ]
    }
  },
  computed: {
    ...mapState([
      "myHead"
    ])
  },
  methods: {
    getInputValue(inputValue){
      this.inputValue = inputValue
    }
  }
}
</script>

<style lang="scss">
#chat {
}
</style>



