<template>
  <div id="item" v-wechat-title="$route.meta.title">
    <ItemImages
  		:images='item.detail.images'
  	></ItemImages>
  	<ItemContent
  		:detail='item.detail'
  	></ItemContent>
  	<ItemComments
  		:comments='item.comments'
  	></ItemComments>
    <ItemCustomer
      :user='item.user'
    ></ItemCustomer>
    <ItemBottom
      :like='item.detail.like'
    ></ItemBottom>
    <Background></Background>
  </div>
</template>

<script>
import ItemImages from './ItemImages.vue'
import ItemContent from './ItemContent.vue'
import ItemComments from './ItemComments.vue'
import ItemCustomer from './ItemCustomer.vue'
import ItemBottom from './ItemBottom.vue'
import Background from '@/components/background/Background.vue'
import { getItem } from '@/api/article'

export default {
  components: {
    ItemImages,
    ItemContent,
    ItemComments,
    ItemCustomer,
    ItemBottom,
    Background
  },
  data () {
    return {
      item: {
        detail: {
          name: null,
          price: null,
          originalPrice: null,
          releaseTime: null,
          introduction: null,
          views: null,
          like: {
            total: null,
            own: null
          },
          isColl: null,
          images: []
        },
        user: {
          id: null,
          name: null,
          headImage: null,
          lastLoginTime: null
        },
        comments: []
      }
    }
  },
  mounted () {
    getItem(this.$route.params.itemId).then(response => {
      const data = response.data
      if (data.code == 200) {
        this.item.detail.name = response.content.name
        this.item.detail.price = response.content.price
        this.item.detail.originalPrice = response.content.price
        this.item.detail.releaseTime = response.content.tmCreated
        this.item.detail.introduction = response.content.desc
        this.item.detail.images = response.content.images
        this.item.detail.views = response.content.views
        this.item.detail.like.total = response.content.likes
        this.item.detail.like.own = response.content.isLike
        this.item.detail.isColl = response.content.isColl

        this.item.user.id = response.content.mrktUser.uid
        this.item.user.name = response.content.mrktUser.nName
        this.item.user.headImage = response.content.mrktUser.avatar
        this.item.user.lastLoginTime = response.content.mrktUser.tmLstLgn

        this.item.comments = response.content.comments
      } else {
        console.error(data.message)
      }
    }).catch(error => {
      console.error(error)
    })
  }
}
</script>

<style lang="scss">
#item {
  text-align: left;
}
</style>



