<template>
  <div id="chatContent">
    <div
      class="message"
      v-for="(message,index) of messages"
      :key="index"
    >
      <img
        :src="userHead"
        class="userHead"
        v-if="message.owner==0"
      >
      <img
        :src="myHead"
        class="userHead my-head"
        v-if="message.owner==1"
      >
      <div
        class="text"
        :class="{'my-text':(message.owner==1)}"
      >
        <div
          class="triangle"
          :class="{'my-triangle':(message.owner==1)}"
        >
        </div>
        {{ message.content }}
      </div>
      
    </div>
  </div>
</template>

<script>
export default {
  components: {

  },
  props: {
    messages: {
      type: Array
    },
    userHead: {
      type: String
    },
    myHead: {
      type: String
    }
  }
}
</script>

<style lang="scss">
#chatContent {
  text-align: left;
  .message {
    width: 100%;
    min-height: 60px;
    .userHead {
      float: left;
      width: 40px;
      height: 40px;
      margin: 10px 10px;
      &.my-head {
        float: right;
      }
    }
    .text {
      font-weight: 500;
      float: left;
      margin-top: 10px;
      padding: 7px 10px;
      border-radius: 4px;
      background: white;
      display: inline-block;
      max-width: 65%;
      &.my-text {
        float: right;
        background: rgb(160,231,90);
      }
      .triangle {
        width: 10px;
        height: 10px;
        background: white;
        display: inline-block;
        float: left;
        transform: rotate(45deg);
        position: relative;
        margin-right: -15px;
        left: -15px;
        top: 5px;
        &.my-triangle {
          float: right;
          left: 0px;
          background: rgb(160,231,90);
        }
      }
    }
  }
}
</style>



